@import url(https://fonts.googleapis.com/css2?family=Parisienne&display=swap);
* {
  margin: 0;
  box-sizing: 0;
  background-color: #0a1c32;
}

.header {
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 5rem;
}

h1 {
  font-family: "Parisienne", cursive;
  font-size: 4rem;
  color: #3fbdde;
  width: 17.5rem;
  text-align: center;
}

.main-container {
  width: 15rem;
}

.container input {
  width: 17.2rem;
  height: 2rem;
  margin-bottom: 0.5rem;
  border: none;
  background-color: #3fbdde;
  border-radius: 8px;
  text-align: right;
  font-size: larger;
}

.keypad {
  width: 17.5rem;
}

.keypad .num-btn {
  padding: 9px;
  border: none;
  color: #3fbdde;
  background-color: #222222;
  margin: 3px;
  border-radius: 8px;
}
.keypad .num-btn:hover {
  transition-duration: 0.2s;
  background-color: #3fbdde;
  color: #222222;
  cursor: pointer;
}

.clear-all {
  width: 4rem;
  height: 3rem;
  color: #222222;
  background-color: #3fbdde;
  padding: 9px;
  border: none;
  margin: 3px;
  border-radius: 8px;
}

.clear {
  width: 4rem;
  height: 3rem;
  color: #222222;
  background-color: #3fbdde;
  padding: 9px;
  border: none;
  margin: 3px;
  border-radius: 8px;
}

.num-btn {
  width: 4rem;
  height: 3rem;
}

.keypad .symbol {
  width: 4rem;
  height: 3rem;
  background-color: #3fbdde;
  color: #222222;
  padding: 9px;
  border: none;
  margin: 3px;
  border-radius: 8px;
}

.keypad .result {
  width: 8.3rem;
  height: 3rem;
  background-color: #3fbdde;
  color: #222222;
  padding: 9px;
  border: none;
  margin: 3px;
  border-radius: 8px;
}

.clear:hover,
.clear-all:hover,
.result:hover,
.symbol:hover {
  transition-duration: 0.2s;
  background-color: #222222;
  color: #3fbdde;
  cursor: pointer;
}

